import React, { Component, Fragment } from 'react';
import { 
  Row, 
  Form, 
  Modal,
  Spin,
  Input
} from 'antd';
import { 
  TableData, 
} from 'components/common';
import { 
  getMinSelfChooseCredit, 
  saveMinSelfChooseCredit 
} from 'services/programDefinition';

@Form.create({ name: 'ModalTCTC' })
class ModalTCTC extends Component {
  state = {
    paramsSearch: {},
    loading: false,
    reload: 0,
  };

  componentWillReceiveProps(nextProps) {
    if(this.props.paramsSearch !== nextProps.paramsSearch) {
      this.setState({
        paramsSearch: {...nextProps.paramsSearch},
      },
      () => this.setState({
        reload: this.state.reload + 1,
      }),
      );
    }
    if(nextProps.loading !== this.state.loading) { 
      this.setState({
        loading: nextProps.loading,
      });
    }
  }

  handleSave = () => {
    this.props.form.validateFields(async(_errors, values) => {
      const { paramsSearch } = this.props;
      try {
        this.setState({
          loading: true,
        });
        const details = [];
        const tempObj = {};
        Object.keys(values).forEach((key) => {
          const keyOfRow = key.split('-')[0];
          const id = key.split('-')[1];
          if(!tempObj[id]) {
            tempObj[id] = {};
            tempObj[id].programId = id;
          }
          tempObj[id][keyOfRow] = parseInt(values[key]);
        });
        Object.keys(tempObj).forEach((key) => {
          details.push(tempObj[key]);
        });
        await saveMinSelfChooseCredit({
          programId: paramsSearch.programId,
          details: [...details],
        });
        this.props.onSuccess();
      } catch (error) {
        this.setState({
          loading: false,
        });
      }
    });
  }

  handleCancel = () => {
    this.props.onCancel();
  }

  columnsCTDT = [
    {
      title: 'Chương trình',
      dataIndex: 'programLabel',
      key: 'programLabel',
    },
    {
      title: 'Kỳ thứ',
      dataIndex: 'semesterNo',
      key: 'semesterNo',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        console.log('record: ', record);
        return (
          <Form.Item>
            {getFieldDecorator(`semesterNo-${record.semesterNo}`, {initialValue: record.semesterNo})(
              <Fragment>{record.semesterNo}</Fragment>
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Nhóm tự chọn',
      dataIndex: 'selfChoosenGroup',
      key: 'selfChoosenGroup',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`selfChoosenGroup-${record.semesterNo}`, {initialValue: record.selfChoosenGroup})(
              <Fragment>{record.selfChoosenGroup}</Fragment>
            )}
          </Form.Item>
        );
      },
    },
    {
      title: 'Số tín chỉ tự chọn',
      dataIndex: 'noOfMinCredit',
      key: 'noOfMinCredit',
      render: (_text, record) => {
        const { getFieldDecorator } = this.props.form;
        return (
          <Form.Item>
            {getFieldDecorator(`noOfMinCredit-${record.semesterNo}`, {initialValue: record.noOfMinCredit})(
              <Input />
            )}
          </Form.Item>
        );
      },
    },

  ];

  render() {
    const { 
      paramsSearch, 
      loading,
      reload
    } = this.state;
    return (
      <Modal
        title='Tín chỉ tự chọn'
        visible={this.props.visible}
        onOk={this.handleSave}
        okText="Lưu"
        onCancel={this.handleCancel}
        cancelText="Hủy"
        width={800}
      >
        <Spin spinning={loading}>
          <Form layout="vertical" onSubmit={this.handleSave}>
            <Fragment>
              <Row gutter={24}>
                <TableData
                  reload={reload}
                  columns={this.columnsCTDT}
                  service={getMinSelfChooseCredit}
                  paramSearch={paramsSearch}
                  allowGetData={!!paramsSearch.programId}
                  rowKey="semesterNo"
                />
              </Row>
            </Fragment>
          </Form>
        </Spin>
        
      </Modal>
    );
  }
}

export default ModalTCTC;



