import { request } from 'utils';

const prefix = '/api/education';

export const getProgramDefinition = (
  { 
    trainTypeId,
    facultyId,
    studyFieldId,
    courseId,
    page, 
    pageSize = 10, 
    orderBy, 
    isPagingEnabled = true,
  }
) =>
  request({
    prefix,
    url: `/ProgramDefinition`,
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      studyFieldId,
      courseId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getProgramDefinitionById = ({ id }) =>
  request({
    prefix,
    url: `/ProgramDefinition/${id}`,
    method: 'GET',
  });

export const deleteProgramDefinition = id =>
  request({
    prefix,
    url: `/ProgramDefinition/${id}`,
    method: 'DELETE',
  });

export const addProgramDefinition = ({ 
    programNo,
    programId,
    trainTypeId,
    facultyId,
    courseId,
    studyFieldId,
    noOfCredit,
    noOfSemester,
    createProgramDetails
  }) =>
    request({
      prefix,
      url: `/ProgramDefinition`,
      data: {
        programNo,
        programId,
        trainTypeId,
        facultyId,
        courseId,
        studyFieldId,
        noOfCredit,
        noOfSemester,
        createProgramDetails
      },
      method: 'POST',
    });

  export const editProgramDefinition = ({ 
      id,
      programNo,
      programId,
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      noOfCredit,
      noOfSemester,
      createProgramDetails
    }) =>
      request({
        prefix,
        url: `/ProgramDefinition/${id}`,
        data: {
          id,
          programNo,
          programId,
          trainTypeId,
          facultyId,
          courseId,
          studyFieldId,
          noOfCredit,
          noOfSemester,
          createProgramDetails
        },
        method: 'PUT',
      });

export const duplicateToNewCourse = ({ 
  programId,
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
}) =>
  request({
    prefix,
    url: `/ProgramDefinition/DuplicateToNewCourse`,
    data: {
      programId,
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
    },
    method: 'POST',
  });

export const uploadFilePrograms = (file) => {
  const formData = new FormData();
  formData.append('file', file);
  return request({
    prefix,
    url: `/ProgramDefinition/UploadFile`,
    method: 'POST',
    params: {},
    data: formData,
    headers: { 'Content-Type': 'multipart/form-data' },
    
  });
};

export const getProgramByClass = (
  { 
    programId,
    studyFieldId,
    page, 
    pageSize = 10, 
    orderBy, 
    isPagingEnabled = true,
  }
) =>
  request({
    prefix,
    url: `/ProgramDefinition/ProgramByClass`,
    method: 'GET',
    params: {
      programId,
      studyFieldId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const createAssignProgramToClass = ({ programId, classIds }) =>
  request({
    prefix,
    url: `/ProgramDefinition/AssignProgramToClass`,
    data: {
      programId,
      classIds
    },
    method: 'POST',
  });

  export const getMinSelfChooseCredit = (
    { 
      programId,
      page, 
      pageSize = 10, 
      orderBy, 
      isPagingEnabled = true,
    }
  ) =>
    request({
      prefix,
      url: `/ProgramDefinition/MinSelfChooseCredit`,
      method: 'GET',
      params: {
        programId,
        page,
        pageSize,
        orderBy,
        isPagingEnabled,
      },
      data: {},
    });

export const saveMinSelfChooseCredit = ({ programId, details }) =>
  request({
    prefix,
    url: `/ProgramDefinition/SaveMinSelfChooseCredit`,
    data: {
      programId,
      details
    },
    method: 'POST',
  });


